import { html } from 'lit';
import { WrapperComponent } from "../../wrapper-component";
import { state } from "lit/decorators.js";
import { style as globalStyle } from "../../style";
import { style as localStyle } from "./style";


export class Component extends WrapperComponent {
  static styles = [globalStyle, localStyle]
  @state() 
  collection : string = '3390567'

  @state() 
  source: string = `https://source.unsplash.com/collection/${this.collection}`;

  @state() 
  url: string;

  @state() 
  response: object;

  connectedCallback(){
    super.connectedCallback()
    fetch(this.source)
    .then( response => { 
      if( response.ok ) {
        this.url = response.url
      }
      
    } )
    .catch(error => {
      console.log(error);
    })
    
  }

  render() {
    return html`
      <div class="${this.colorSystem} ${this.fontSystem}">

      <section class="content">
        <slot name="header"></slot>
        <slot name="content"></slot>
      </section>
      <section class="image">
        <img src="${this.url}" alt="random image">
      </section>
      </div>
    `;
  }
}
